
.container {
    padding: 0.2em;
}

.container.grid {
    width: 50%;
}
.container.list {
    width: 100%;
}

.video {
    background-color: rgb(119, 118, 118);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    border: 2px solid rgb(53, 52, 49);
    box-shadow: 4px 8px 7px 2px #383733;
    cursor: pointer;
    transition: transform 250ms ease-in;
}

.video:hover {
  transform: scale(1.02);
}

.thumbnail {
  width: 40%;
  height: 100%;
}

.metadata {
  margin-left: 0.2em;
}

.title,
.channel {
  margin: 0;
  font-size: 0.8rem;
  
}

.channel {
  font-size: 0.6rem;
}

